.dashboard-container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 16px;
  }
  
  .dashboard-title {
    font-size: 1.875rem;
    text-align: center;
    margin-bottom: 1rem;
  }
  
  .tabs-list {
    display: flex;
    gap: 12px;
  }
  
  .tab-trigger {
    padding: 10px 20px;
    font-size: 18px;
    font-weight: bold;
    border-radius: 8px;
  }
  
  .dropdown-container {
    margin-bottom: 1rem;
  }
  
  .select-trigger {
    width: 100%;
    max-width: 200px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 8px;
    font-size: 16px;
  }
  
  .chart-container {
    display: flex;
    align-items: flex-start;
    gap: 12px;
  }
  
  .labels-container {
    width: 120px;
    display: flex;
    flex-direction: column;
  }
  
  .label-item {
    height: 55px;
    align-items: center;
  }
  